@import url(https://fonts.googleapis.com/css?family=Abel);
@import url(https://fonts.googleapis.com/css?family=Dosis);
@import url(https://fonts.googleapis.com/css?family=Oxygen|Quicksand);
/*General*/
html,
body {
  font-family: "Abel", sans-serif;
  font-size: 18px;
  text-align: center;
  min-width: 320px;
}

* {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 33px;
}

h3 {
  font-size: 23px;
  padding: 15px;
}

a {
  color: #91e3ff;
}

/*Nav*/
nav {
  height: 65px;
  background-color: #595959;
  position: fixed;
  text-align: right;
  width: 100%;
  z-index: 2;
}

nav ul {
  font-size: 16px;
  text-transform: uppercase;
  padding: 10px 0px 0px 0px;
}

nav ul li {
  display: inline-block;
  margin: 10px 45px 10px 10px;
  transition: 0.2s;
  font-size: 18px;
}

nav ul li:hover {
  -webkit-transform: rotate(7deg);
          transform: rotate(7deg);
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

#main {
  width: 100%;
}

/*Sections*/
section {
  width: 100%;
  height: 100%;
}

section .wave {
  position: absolute;
  width: 100%;
  height: 143px;
  bottom: 0;
  left: 0;
  background: url(https://farm8.staticflickr.com/7898/47085590142_8cf6652f53_o.png);
  -webkit-animation: animate 10s linear infinite;
          animation: animate 10s linear infinite;
}

section .wave:before {
  content: "";
  width: 100%;
  height: 143px;
  background: url(https://farm8.staticflickr.com/7898/47085590142_8cf6652f53_o.png);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.4;
  -webkit-animation: animate-reverse 10s linear infinite;
          animation: animate-reverse 10s linear infinite;
}

section .wave:after {
  content: "";
  width: 100%;
  height: 143px;
  background: url(https://farm8.staticflickr.com/7898/47085590142_8cf6652f53_o.png);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.4;
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  -webkit-animation: animate 20s linear infinite;
          animation: animate 20s linear infinite;
}

/*Welcome*/
.welcome {
  top: 0;
  background: #000;
  min-height: 55vh;
  padding-top: 45vh;
}

#welcome-section {
  text-align: center;
  color: white;
}

p#bio {
  padding: 10px;
}

/*Achievements*/
#achievements {
  padding: 100px 40px 100px 65px;
}

#responsive-web {
  padding: 25px 20px 50px 20px;
}

#achievement-title {
  margin-bottom: 15px;
}

.project-tile {
  text-decoration: none;
  color: #000;
  text-align: center;
  display: inline-block;
  overflow: hidden;
  border: 2px solid black;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  height: 220px;
  width: 325px;
  margin: 30px 50px 30px 45px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.39), 0 10px 10px rgba(0, 0, 0, 0.23);
}

.project-tile:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}

.project-img {
  display: block;
  max-width: 100%;
}

#profile-link {
  border: 2px;
  border-style: solid;
  padding: 10px;
  border-radius: 5px;
  text-decoration: none;
  color: black;
  display: block;
  text-align: center;
  width: 150px;
  margin: 20px auto 85px;
}

.project {
  text-decoration: none;
  color: #008080;
  display: inline-block;
  overflow: hidden;
  border: 2px solid lightblue;
  height: 220px;
  width: 280px;
  margin: 40px 100px;
}

.project-pic {
  width: 100%;
  height: 180px;
  border-bottom: 2px solid lightblue;
}

.project-title {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  overflow: hidden;
  border: 2px solid black;
  height: 225px;
  width: 300px;
  margin: 30px 50px 30px 45px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.39), 0 10px 10px rgba(0, 0, 0, 0.23);
}

.show-all {
  text-decoration: none;
  display: block;
  width: 200px;
  height: 30px;
  margin: 50px auto 90px;
  background: #e0ebe8;
  color: #008080;
  font-weight: bold;
  font-size: 1em;
  border: 2px solid #90c695;
  border-radius: 4px;
  cursor: pointer;
  padding-top: 10px;
}

.show-all:hover {
  background: #e4f1fe;
  color: green;
}

/*Social*/
@-webkit-keyframes animate {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 1360px;
  }
}
@keyframes animate {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 1360px;
  }
}

@-webkit-keyframes animate-reverse {
  0% {
    background-position: 1360px;
  }

  100% {
    background-position: 0;
  }
}

@keyframes animate-reverse {
  0% {
    background-position: 1360px;
  }

  100% {
    background-position: 0;
  }
}

#contact {
  height: 200px;
  padding: 100px;
}

#social {
  margin-top: 70px;
}

#social ul {
  position: relative;
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
}

#social ul li {
  position: relative;
  list-style: none;
  width: 50px;
  height: 50px;
  margin: 0 30px;
  -webkit-transform: rotate(-30deg) skew(25deg);
          transform: rotate(-30deg) skew(25deg);
  background: #ccc;
}

#social ul li span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  transition: 0.5s;
  display: -webkit-flex;
  display: flex;
  -webkit-animation-name: social;
          animation-name: social;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: #000;
  font-size: 30px;
}

.list-social-items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

ul li:hover span {
  box-shadow: -1px 1px 1px rgb(0, 0, 0);
}

ul li:hover span:nth-child(5) {
  -webkit-transform: translate(40px, -40px);
          transform: translate(40px, -40px);
  opacity: 1;
}

ul li:hover span:nth-child(4) {
  -webkit-transform: translate(30px, -30px);
          transform: translate(30px, -30px);
  opacity: 0.8;
}

ul li:hover span:nth-child(3) {
  -webkit-transform: translate(20px, -20px);
          transform: translate(20px, -20px);
  opacity: 0.6;
}

ul li:hover span:nth-child(2) {
  -webkit-transform: translate(10px, -10px);
          transform: translate(10px, -10px);
  opacity: 0.4;
}

ul li:hover span:nth-child(1) {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  opacity: 0.2;
}

/*Footer*/
footer {
  background-color: #000;
  color: #999999;
  text-align: center;
  padding: 15px;
}

/*Queries*/
@media (max-width: 800px) {
  h1 {
    font-size: 30px;
  }

  h2 {
    font-size: 25px;
  }

  h3 {
    font-size: 20px;
    padding: 15px;
  }

  #social ul li {
    width: 40px;
    height: 40px;
  }

  #social ul li span {
    font-size: 20px;
  }
}

@media (max-width: 550px) {
  nav ul {
    padding: 10px 0px 0px 0px;
  }

  nav ul li {
    margin: 10px 15px 10px 10px;
  }

  #social {
    margin-top: 40px;
  }

  #social ul li {
    margin: 0 30px;
  }

  #achievements {
    padding: 100px 0px 100px 0px;
  }

  .project-tile {
    height: 200px;
    width: 300px;
    margin: 15px 5px 15px 3px;
  }

  #about-me {
    margin-top: -20%;
    padding: 3%;
  }
}

@media (max-width: 375px) {
  #about-me {
    margin-top: -35%;
    padding: 1%;
  }

  #social {
    margin-top: 50%;
  }

  #social ul li {
    width: 35px;
    height: 30px;
    margin: 0 25px;
  }
}

@media (max-height: 770px) {
  #social ul {
    margin-bottom: 5%;
  }

  #social ul li span {
    -webkit-animation-name: social;
            animation-name: social;
    -webkit-animation-duration: 8s;
            animation-duration: 8s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

  .wave {
    display: none;
  }

  @-webkit-keyframes social {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
      opacity: 0.2;
    }

    50% {
      -webkit-transform: translate(15px, -15px);
              transform: translate(15px, -15px);
      opacity: 1;
    }

    100% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
      opacity: 0.2;
    }
  }

  @keyframes social {
    0% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
      opacity: 0.2;
    }

    50% {
      -webkit-transform: translate(15px, -15px);
              transform: translate(15px, -15px);
      opacity: 1;
    }

    100% {
      -webkit-transform: translate(0, 0);
              transform: translate(0, 0);
      opacity: 0.2;
    }
  }
}

@media (max-width: 320px) {
  #about-me {
    margin-top: -50%;
  }
}

